@import "../../global.scss";
.works{
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;

        @include mobile{
            display: none;
        }

        &.left{
            left: 100px;
            transform: rotateY(180deg);
        }
        &.right{
            right: 100px;
        }
    }



    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile{
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }

    .container{
        width: 100vw;
        size: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        .item{
            width: 750px;
            height: 100%;
            background-color: white;
            border-radius: 20px;
            display: flex;
            align-items: center;
            justify-content: center;

            @include mobile{
             width: 90%;
             height: 320px;
             margin: 10px 0;
            }

            .first{
                flex: 4;
                height: 80%;
                display: flex;
                

                .firstContainer{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    .imgContainer{
                        width: 285px;
                        height: 100px;
                        align-items:top;
                        
                        display: flex;
                       

                        img{
                            width: 855px;
                            height: 100px;
                        }
                    }
                 h2{
                     font-size: 20px;
                 }
                p{
                    font-size: 20px;
                }
                span{
                    font-size: 12px;
                    font-weight: 600;
                    text-decoration: underline;
                    cursor: pointer;
                }
                }
               
            }
            .second{
                flex: 4;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 30px;;

                img{
                    width: 700px;
                    transform: rotate(-10deg);
                    
                }
               
            }
            .third{
                flex: 4;
                height: 100%;
              
            }
        }
    }

    
    }
}
