@import "../../global.scss";

.testimonials{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include mobile{
        justify-content: space-around;
    }

    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
        }
    }
    .container{
        width: 200%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile{
            flex-direction: column;
            height: 100%;
        }

        .card{
            width: 220px;
            height: 70%;
            border-radius: 10px;
            box-shadow: 0px 0px 15px -8px black;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            padding: 10px;
            transition: all 1s ease;

            @include mobile{
                height: 180px;
                margin: 5px 0;
            }

            &.featured{
                width: 300px;
                height: 75%;
                margin: 0 10px;

                @include mobile{
                    width: 480px;
                    height: 500px;
                    margin: 1px;
                }
                
            }

            &:hover{
                transform: scale(1.1);
            }

            .top{
                display: flex;
                align-items: center;
                justify-content: center;

                img{
                   
                    &.user{
                        height: 80px;
                        width: 270px;
                        
                        object-fit: cover;
                        margin:0px;

                       
                    }
                }
            }
            .center{
                padding: 10px;
                border-radius: 10px;
                background-color: rgb(235, 225, 227);

               
            }
            .bottom{
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                h3{
                    margin-bottom: 5px;
                    
                }
            }
        }
    }
}